.cardHeight {
  min-height: 120px;
  max-height: auto;
  border: 2px solid black;
  border-radius: 5px;
}

.cardImg {
  width: 100%;
  height: auto;
  min-height: 320px;
  max-height: 320px;
  border-radius: 10%;
}

.categoryBackground {
  background-color: #00618d;
}

.buttonPadding {
  padding: auto;
}

@media only screen and (max-width: 1100px) {
  .cardHeight {
    min-height: 100px;
    max-height: auto;
    border: 2px solid black;
    border-radius: 5px;
  }

  .cardImg {
    width: 100%;
    height: auto;
    border-radius: 10%;
  }

  .cardLinks {
    margin-top: 20px;
  }
}

@media only screen and (max-width: 690px) {
  .cardHeight {
    min-height: 100px;
    max-height: auto;
    border: 2px solid black;
    border-radius: 5px;
  }

  .cardImg {
    width: 100%;
    height: auto;
    border-radius: 10%;
  }

  .cardLinks {
    margin-top: 20px;
  }
}
